@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Cinzel:wght@700&display=swap');

nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4.5rem;
    display: flex;
    justify-content: space-between;  
    background-color: #0a192f;
    align-items: center;
    /* padding: 0 90px; */
    transition: background-color 0.4s ease-in-out;
    z-index: 1000;
}

.logo {
    font-size: 28px;
    color: white;
    font-family: 'Playfair Display', serif;
    text-transform: capitalize;
    font-weight: bold;
    margin-left: 6.7rem;
    margin-top: 0.4rem;
    padding: 12px;
}
ul{
    display: flex;
    list-style: none;
    margin-right: 90px;
}
ul li{
    padding: 3px;
    height: 20px;
    margin: 5px 14px 5px 0px;
}
ul li a{
    color: white;
    text-decoration: none;
    cursor: pointer;
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    text-transform: capitalize;
    font-size: 18px; 
    transition: color 0.3s ease-in-out;
}

.desktop-menu li a:hover {
    color: #4da8ff;
}

@media (max-width:992px){
    ul{
        padding: 20px;
        margin: 0 auto;
    }
}


/* Hamburger Menu */
.hamburger {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.bar {
    width: 25px;
    height: 3px;
    background: white;
    margin: 4px 0;
    transition: 0.4s;
}

/* Mobile View: Show Hamburger */
@media (max-width: 992px) {
    .logo {
        margin-left: 0.9rem;
    }
    .hamburger {
        display: flex;
        margin-right: 3rem;
    }
    .desktop-menu{
        display: none;
    }
}

/* Dropdown Menu Styling */
.dropdown-menu {
    display: none;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background: #0a192f;
    text-align: center;
    padding: 20px;
    transition: transform 0.4s ease-in-out;
}

/* Show menu when `isOpen` is true */
.dropdown-menu.open {
    display: flex;
    flex-direction: column;
}

/* Dropdown menu items */
.dropdown-menu li {
    margin: 10px 0;
    list-style: none;
}

.dropdown-menu li a {
    color: white;
    text-decoration: none;
    font-size: 18px;
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    transition: color 0.3s ease-in-out;
}

.dropdown-menu li a:hover {
    color: #4da8ff;
}

.bar.open:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.bar.open:nth-child(2) {
    opacity: 0;
}

.bar.open:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
}