.work-container{
    background-color: #10223e;
    color: white;
    min-height: 100vh; 
    height: auto;
    background-color: #10223e;
    display: flex;
    flex-direction: column;
    justify-content: center; 
}

.work-heading{
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    margin: 50px 0px 25px 0px;
}

.work-heading span {
    color: #4da8ff;
}

.my-work{
    margin-top: 40px;
    margin-bottom: 8px;
}

.work-heading hr{
    width: 110px;
    background-color: 1px solid white;
    height: 2px;
}

.display-data{
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 1rem;
    margin: 0px 70px 0px 80px;
}

@media (min-width: 992px) {
    .work-heading{
        margin: 50px 0px 25px 10px;
    }
    .display-data {
        display: grid;
        grid-template-columns: repeat(3, 1fr); /* ✅ 3 columns for larger screens */
        gap: 2rem;
    }
}
@media (max-width: 768px) {
    .display-data {
        margin-left: 40px;
    }
}