.footer-container{
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #10223e;
    border-bottom: 1px solid transparent;
    padding-top: 25px;
}
.footer-container hr{
    margin-bottom: 30px;
    width: 85%;
    background-color: white;
    opacity: 0.7;
}
.footer-logo{
    font-size: 28px;
    color: white;
    font-family: 'Playfair Display', serif;
    text-transform: capitalize;
    font-weight: bold;
    margin-top: 0.4rem;
    margin-bottom: 0.4rem;
    padding: 12px;
}

.footer-info{
    width: 80%;
    margin: 0 auto;
    text-align: center;
}
.footer-info p{
    margin-top: 1px;
    font-size: 17px;
    color: white;
}
.copyright{
    margin-right: 2px;
    vertical-align: middle;
    color: white;
}
.res{
    color: white;
}

.icon{
    color: #3385D9;
    font-size: 2.3rem;
    margin-right: 1rem;
    place-items: center;
    transition: all .2s ease-in-out;
    display: inline-block;
}

.icon:hover{
    transform: scale(1.5);
    color: #4da8ff; /* Black Text on Hover */
}