
.hero-section {
    min-height: 100vh; 
    height: 100dvh;
    display: flex;
    align-items: center; 
    justify-content: center;
    /* background-color: rgb(26, 42, 58); */
    background-color: #0a192f;
}
.container{
    justify-content: space-between;
    max-width: 95%;
    height: 600px;
    display: flex;
    /* border: 2px solid red; */
}
.container-left{
    width: 35rem;
    margin-left: 50px;
}
.container-heading{
    /* border: 2px solid red; */
    position: relative;
    top: 90px;
    left: 15px;
    width: 30rem;
    /* border: 2px solid red; */
}
.container-heading p{
    font-size: 20px;
    color: white;
}
.hello{
    /* border: 2px solid red; */
    display: inline-block;
    color:white;
    font-style: bold;
    margin: 0 auto;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 48px;  /* Adjust based on preference */
    font-weight: bold;
    color: white; /* Ensure visibility */
}

#hire-me-btn{
    font-size: 1.2rem;
    font-weight: bold;
    padding: 12px 24px;
    border: 2px solid #4da8ff; /* Light Blue Border */
    color: #4da8ff; /* Text Color */
    background: transparent;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
  
#hire-me-btn:hover {
background: #4da8ff; /* Light Blue Background */
color: #000; /* Black Text on Hover */
}

.icon-list{
    margin-top: 13rem;
}
.icon{
    color: #3385D9;
    font-size: 2.3rem;
    margin-right: 1rem;
    place-items: center;
    transition: all .2s ease-in-out;
    display: inline-block;
    /* border: 2px solid red; */
}
.icon:hover{
    transform: scale(1.5);
    color: #4da8ff; /* Black Text on Hover */
}
.container-right{
    /* border: 2px solid white; */
    margin-top: 55px;
    margin-left: 50px;
    margin-right: 0px;
    margin-top: 30px;
    height: 500px;
    /* width: 25%; */
}
.hero-img-out {
    position: relative; /* Ensures ::before aligns properly */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32rem; /* Slightly larger than img to avoid collision */
    height: 32rem;
}

.hero-img-out::before {
    content: "";
    position: absolute;
    width: 32rem; /* Larger than image */
    height: 32rem;
    border-radius: 50%;
    border: 3px solid transparent;
    border-left-color: rgba(255, 255, 255, 0.7);
    border-right-color: rgba(255, 255, 255, 0.7);
}

.container img {
    width: 30rem;
    height: 30rem;
    opacity: 0.9;
    border-radius: 50%;
    position: relative;
    border: 2px solid rgba(255, 255, 255, 0.3);
}


/* Adjusts circle size and position for smaller screens */
@media (max-width: 1255px) {
    .circle {
        width: 100px;
        height: 100px;
    }
}

@media (max-width: 1125px) {
    .container-right{
        display: none;
    }
    .container img {
        width: 30rem;
        height: 20rem;
    }
    .circle {
        width: 80px;
        height: 80px;
    }
    .container-heading {
        font-size: 18px;
        position: relative;
        left: 0;
        text-align: center;
    }
}

@media (max-width: 990px) {
    .container img {
        display: none; 
        border: none;
    }
    .container-heading {
        text-align: center;
        margin: 0 auto;
    }
    .circle {
        width: 70px;
        height: 70px;
        position: static; 
        margin: 10px auto; 
    }
}

/* ✅ Center Everything in Mobile View */
@media (max-width: 768px) {
    .container {
        flex-direction: column;
        align-items: center;
        text-align: center; /* Ensures text is centered */
    }

    .container-left {
        /* border: 2px solid red; */
        width: 100%;
        margin: 0 auto;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .container-heading {
        width: 100%;
        text-align: center;
    }

    .hello {
        font-size: 2rem; 
        text-align: center;
    }

    .basic-info {
        text-align: center;
        max-width: 100%;
    }
    .basic-info p{
        font-size: 1rem;
    }

    .icon-list {
        display: flex;
        justify-content: center;
        gap: 15px;
        margin-top: 17rem;
    }

    .icon {
        font-size: 1.8rem;
    }

    /* ✅ Center Button */
    #hire-me-btn {
        margin: 10px auto;
        display: block;
        margin-top: 70px;
        top: 20px;
    }
}
