.project-box{
    width: 300px;
    margin: 0 auto;
}
.main{
    width: 300px;
    height: 290px;
    position: relative;
    /* border: 1.5px solid rgba(255, 255, 255, 0.8);  */
    border: 2px solid #4da8ff;
    border-radius: 12px;
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
    margin-bottom: 20px;
}
.main:hover{
    box-shadow: 10px 10px 30px rgba(249, 247, 247, 0.4); /* Larger shadow on hover */
    transform: translateZ(-5px);
}
.img-fluid{
    width: 280px;
    height: 150px;
    margin-left: 10px;
    margin-top: 10px;
    border-radius: 10px;
}
.image_overlay{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    height: 297px;
    width: 99%;
    /* background-color:  #3385D9; */
    background-color: black;
    color: white;
    align-items: center;
    text-align: center;
    opacity: 0;
    transition:opacity 0.5s;
    border: 2px solid black;
}
.view-btn-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
#button-view{
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 10px;
    padding: 8px 20px;
    border: 2px solid #4da8ff; /* Light Blue Border */
    color: #4da8ff; /* Text Color */
    background: transparent;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    justify-content: center;
}
#button-view:hover {
    background: #4da8ff; /* Light Blue Background */
    color: #000; /* Black Text on Hover */
}

.image_overlay:hover{
    opacity: 0.7;
    background-position: top;
}
.hover-title{
    line-height: 20px;
    font-size: 30px;
    color: white;
}
.hover-description{
    /* border: 2px solid red; */
    color: rgba(255, 255, 255, 0.8); 
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 0.2);
    margin-top: 0px;
    margin-bottom: 2px;
    margin-left: 10px;
    font-weight: normal;
    padding: 1px;
    height: 4.8rem;
}
.project-link{
    text-decoration: none;
    font-weight: normal;
}