.contact-main{
    background-color: #10223e;
    color: white;
    text-align: center;
    min-height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center; 
}
.contact-heading{
    text-align: center;
    font-size: 30px;
    color: white;
    margin: 50px 0px 25px 10px;
}
.my-contact span {
  color: #4da8ff;
}
/* .contact-heading hr{
    width: 100px;
    background-color: 1px solid white;
    height: 2px;
} */
.contact-form form{
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
    width: 55%;
    margin: 5px auto;
    /* border: 1.5px solid rgba(255, 255, 255, 0.8);  */
    border-radius: 10px;
    height: 30rem;
}
.name-fields {
    display: flex;
    justify-content: space-between; /* Ensures even spacing */
    gap: 10px; /* Adds space between first and last name */
    width: 100%;
}

#first-name, #last-name{
    width: 48%;
    margin-bottom: 20px;
}
#email{
    margin-bottom: 20px;
}
#message {
    /* width: 15rem; */
    height: 15rem;
    resize: none; 
    margin-top: 20x;
    margin-bottom: 20px;
}
#first-name, #last-name, #email, #message{
    padding: 12px 12px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 3px solid #3385D9;
    font-size: 15px;
    border-radius: 5px 5px 0px 0px;
}
input:focus, textarea:focus{
    outline: none;
}

#btn-submit{
    width: 10rem;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 12px 24px;
    border: 2px solid #4da8ff; 
    color: #4da8ff; 
    background: transparent;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

#btn-submit:hover {
    background: #4da8ff; /* Light Blue Background */
    color: rgb(26, 42, 58); /* Black Text on Hover */
}

@media (max-width: 992px) {
    .name-fields {
        flex-direction: column; /* Stack inputs vertically */
        gap: 4px; 
    }

    #first-name, #last-name, #email, #message {
        width: 96%; /* Full width when stacked */
    }
}