.about-container{
    background-color: #10223e;
    color: white;
    text-align: center;
    min-height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center; 
}
.about-heading{
    font-size: 30px;
    font-weight: bold;
}

.about-heading span {
    color: #4da8ff;
}

.about-content{
    margin: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media (min-width: 992px) {
    .about-content {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
}
@media (max-width: 992px) {
    .about-dec{
        margin: 0 auto;
    }
}
.about-dec{
    padding: 20px;
    width: 65rem;
    color: white;
    text-align: center;
    font-size: 18px;
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 0.2);
    max-width: 80%;
    line-height: 1.6;
}
.about-img-div{
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-top: 30px; */
}

.about-img-out {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* First Arc Border */
.about-img-out::before {
    content: "";
    position: absolute;
    width: 220px;
    height: 220px;
    border-radius: 50%;
    border: 2px solid transparent;
    border-left-color: rgba(255, 255, 255, 0.8);
    border-right-color: rgba(255, 255, 255, 0.8);
}

/* Inner Circle */
.about-img {
    border: 2px solid rgba(255, 255, 255, 0.8);
    width: 180px; /* Inner circle size */
    height: 180px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    z-index: 1;
}

/* Image inside Inner Circle */
.about-img img {
    width: 200%;
    height: 130%;
    border-radius: 50%;
    object-fit: cover;
}

