.resume-container {
  background-color: #0a192f;
  color: white;
  text-align: center;
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
  justify-content: center; 
}

.resume-heading {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

.resume-heading span {
  color: #4da8ff;
}

/* Main Resume Layout */
.resume-info-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
}

/* Education & Experience Sections */
.education-container, .exp-container {
  width: 40%;
  text-align: left;
}

/* Timeline Styles */
.timeline {
  position: relative;
  padding-left: 20px;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 8px;
  top: 0;
  height: 100%;
  width: 3px;
  background: #4da8ff;
}

/* Timeline Item */
.timeline-item {
  position: relative;
  background: #112240;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Timeline Dots */
.timeline-item::before {
  content: "";
  position: absolute;
  left: -20px;
  top: 18px;
  width: 12px;
  height: 12px;
  background: #4da8ff;
  border-radius: 50%;
  border: 2px solid #0a192f;
}

/* Date Styling */
.date {
  font-size: 14px;
  color: #4da8ff;
}

/* Headings Inside Timeline Items */
.timeline-item h3 {
  font-size: 18px;
  font-weight: bold;
  margin: 5px 0;
}

#btn-cv{
  margin-top: 15px;
  margin-bottom: 30px;
  width: 10.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 12px 24px;
  border: 2px solid #4da8ff; 
  color: #4da8ff; 
  background: transparent;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

#btn-cv:hover {
  background: #4da8ff; /* Light Blue Background */
  color: rgb(26, 42, 58); /* Black Text on Hover */
}

/* Responsive Design */
@media (max-width: 768px) {
  .resume-info-container {
    flex-direction: column;
    align-items: center;
  }
  .education-container, .exp-container {
    width: 90%;
  }
}
