.skills-container {
    background-color: #0a192f;
    color: white;
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding: 40px; */
  }
  
  /* Heading Styling */
  .skills-heading {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    /* margin-bottom: 50px; */
    margin: 50px 0px 25px 10px;
  }
  
  .skills-heading span {
    color: #4da8ff;
  }
  
  /* Main Layout */
  .skills-info-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    /* gap: 10px; */
  }
  
  /* Skill Category Section */
  .skills-category {
    width: 40%;
    text-align: center;
    padding: 20px;
    border-radius: 8px;
  }
  
  /* Images inside categories */
  .image-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 10px;
    cursor: pointer;
    /* border: 2px solid rgba(255, 255, 255, 0.3); */
  }
  
  .image-container img {
    width: 100%;
    max-width: 150px;
    height: auto;
    border-radius: 5px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    padding: 5px;
  }
  
  /* Responsive Design */
  @media (max-width: 992px) {
    .skills-info-container {
      flex-direction: column;
      align-items: center;
    }
    .skills-category {
      width: 80%;
    }
  }
  
  @media (max-width: 768px) {
    .skills-heading{
      margin: 50px 0px 25px 10px;
    }
    .skills-category {
      width: 100%;
    }
    .image-container {
      flex-direction: column;
      align-items: center;
    }
  }
  